export default {
	data() {
		return {
			city: "广州",
			inputValue: 0
		}
	},
	onLoad() {

	},
	methods: {
		onKeyInput(e) {
			this.inputValue = e.target.value
		},
		jumpSearch(e) {
			uni.navigateTo({
				url: '../city/city'
			});
		},
		jumpCondition(value) {
			if (value == 1) {
				uni.navigateTo({
					url: '../index/sortAi'
				});
			} else if (value == 2) {
				uni.navigateTo({
					url: '../index/sortPrice'
				});
			}else if(value ==3){
				uni.navigateTo({
					url: '../index/sortSite'
				});
			}else if(value==4){
				uni.navigateTo({
					url: '../index/sortAppoint'
				});
			}
		},
		jumpDetalis(){
			uni.navigateTo({
				url: '../detalis/details'
			});
		}
	}
}
